.no-auth-layout{display:flex;flex:auto;flex-direction:row;min-height:0;background-color:#f2f4f7}.no-auth-layout-banner{flex:auto;height:100vh;background-position:top right;background-size:cover;background-repeat:no-repeat}@media (max-width: 768px){.no-auth-layout-banner{display:none}}.no-auth-layout-sider{position:relative;min-width:0;transition:all 0.2s;flex:0 0 650px;max-width:650px;width:650px}@media (max-width: 1400px){.no-auth-layout-sider{flex-basis:500px;max-width:500px;width:500px}}@media (max-width: 1200px){.no-auth-layout-sider{flex-basis:50%;max-width:50%;width:50%}}@media (max-width: 768px){.no-auth-layout-sider{flex-basis:100%;max-width:100%;width:100%}}.no-auth-layout-sider-children{display:flex;flex-wrap:nowrap;flex-direction:column;height:100vh;max-height:100vh;overflow:auto}.no-auth-layout-sider-content{padding:2.5rem 20%;margin:auto 0}@media (max-width: 1400px){.no-auth-layout-sider-content{padding:2.5rem 15%}}@media (max-width: 1200px){.no-auth-layout-sider-content{padding:2.5rem 10%}}@media (max-width: 768px){.no-auth-layout-sider-content{margin:0}}
